.home {
  position: fixed;
  top: 50px;
  left: 50px;
  transform: translateY(-50%);
  transition: transform 0.3s ease-out;
}

.home-link {
  display: block;
  width: 30px;
  height: 30px;
}

.home-link:hover {
  transform: scale(1.1);
  cursor: pointer;
}

@media (max-width: 768px) {
  .home {
    position: sticky;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .home-link {
    margin-right: 50px;
    margin-top: 50px;
  }
}
