.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #000000;
  color: #ffffff;
  height: 70vh;
  opacity: 0; /* nascondi il contenuto all'inizio */
  transition: opacity 1s ease-in-out; /* animazione per visualizzare gradualmente il contenuto */
}

.container.show {
  opacity: 1; /* mostra il contenuto */
}

.text-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  animation-name: slide-up;
  animation-duration: 1s;
  animation-fill-mode: both;
}

.image-container {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  animation-name: fade-in;
  animation-duration: 2s;
  animation-delay: 1s;
  animation-fill-mode: both;
}

h1 {
  font-size: 4rem;
  margin-bottom: 2rem;
}

h2 {
  font-size: 3rem;
  margin-top: 0;
}

img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.image-container img {
  width: 80%;
  height: auto;
  border-radius: 50%;
}

@media screen and (min-width: 768px) {
  .container {
    flex-direction: row;
  }

  .text-container {
    order: 1;
    margin-right: 3rem;
    text-align: center;
  }

  .image-container {
    order: 2;
    margin-left: 3rem;
  }
  img {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
  }
  .image-container img {
    width: 70%;
  }
}

@keyframes slide-up {
  from {
    transform: translateY(50%);
    opacity: 0;
  }
  to {
    transform: translateY(0%);
    opacity: 1;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
